import * as React from "react"

// styles
const pageStyles = {
  color: "#232129",
  padding: 96,
  fontFamily: "-apple-system, Roboto, sans-serif, serif",
}
const headingStyles = {
  marginTop: 0,
  marginBottom: 50,
  maxWidth: 500,
}
const headingAccentStyles = {
  color: "darkblue",
  fontSize: 20,
}
const paragraphStyles = {
  marginBottom: 48,
}
const codeStyles = {
  color: "#8A6534",
  padding: 4,
  backgroundColor: "#FFF4DB",
  fontSize: "1.25rem",
  borderRadius: 4,
}
const listStyles = {
  marginBottom: 96,
  paddingLeft: 0,
}
const listItemStyles = {
  fontWeight: 300,
  fontSize: 24,
  maxWidth: 560,
  marginBottom: 20,
}

const linkStyle = {
  color: "#8954A8",
  fontWeight: "bold",
  fontSize: 16,
  verticalAlign: "5%",
  marginTop: 20,
}

const docLinkStyle = {
  ...linkStyle,
  listStyleType: "none",
  marginBottom: 24,
}

const descriptionStyle = {
  color: "#232129",
  fontSize: 14,
  marginTop: 10,
  marginBottom: 0,
  lineHeight: 1.25,
}

const docLink = {
  text: "Human-Centered Artificial Intelligence Lab @ UOS",
  url: "https://hcail.uos.ac.kr",
  color: "#8954A8",
}

const badgeStyle = {
  color: "#fff",
  backgroundColor: "#088413",
  border: "1px solid #088413",
  fontSize: 11,
  fontWeight: "bold",
  letterSpacing: 1,
  borderRadius: 4,
  padding: "4px 6px",
  display: "inline-block",
  position: "relative",
  top: -2,
  marginLeft: 10,
  lineHeight: 1,
}

// data
const links = [
  {
    text: "Human-Centered Artificial Intelligence Lab @ UOS",
    url: "https://hcail.uos.ac.kr",
    description:
      "",
    color: "#E95800",
  },
]

const activities = [
  {
    text: "ASSET2021 student volunteer",
    badge: true,
  },
  {
    text: "MobileHCI2021 student volunteer",
    badge: true,
  },
  {
    text: "CHI2021 student volunteer",
  },
  {
    text: "Demo presentation at IUI2021",
    description: "MonoPass: A Password Manager without Master Password Authentication",
    url: "https://hcail.uos.ac.kr"
  },
  {
    text: "CSCW2020 student volunteer",
  },
]

// markup
const IndexPage = () => {
  return (
    <main style={pageStyles}>
      <title>Home Page</title>
      <h1 style={headingStyles}>
        Hyeonhak Jeong
        <br/>
        <span style={headingAccentStyles}>I am a mastser's student at University of Seoul, Department of Computer Science and Engineering </span>
        <li style={docLinkStyle}>
          <a style={linkStyle} href={`${docLink.url}`}>
            {docLink.text}
          </a>
        </li>      
      </h1>
      <ul style={listStyles}>
        <h2 style={headingStyles}>
          Activities
        </h2>
        {activities.map(link => (
          <li key={link.text} style={{ ...listItemStyles, color: link.color }}>
            <span>
              <a
                style={linkStyle}
              >
                {link.text}
              </a>
              {link.badge && (
                <span style={badgeStyle} aria-label="New Badge">
                  NEW!
                </span>
              )}
              <p style={descriptionStyle}>{link.description}</p>
            </span>
          </li>
        ))}
      </ul>
      <p style={paragraphStyles}>
        Contact: drummycrane@gmail.com
      </p>

      <img
        alt="Gatsby G Logo"
        src="data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 2a10 10 0 110 20 10 10 0 010-20zm0 2c-3.73 0-6.86 2.55-7.75 6L14 19.75c3.45-.89 6-4.02 6-7.75h-5.25v1.5h3.45a6.37 6.37 0 01-3.89 4.44L6.06 9.69C7 7.31 9.3 5.63 12 5.63c2.13 0 4 1.04 5.18 2.65l1.23-1.06A7.959 7.959 0 0012 4zm-8 8a8 8 0 008 8c.04 0 .09 0-8-8z' fill='%23639'/%3E%3C/svg%3E"
      />
    </main>
  )
}

export default IndexPage
